.btn_con {
    margin: auto; margin-top: 15px;
    width: 1184px; height: 75px;
}
.btn_con ul {
    width: 100%; height: 100%;
    display: flex;
}
.btn_con ul li {
    /* padding: 23px 0; */
    width: 25%; height: 100%;
    text-align: center;
    border: 2px solid #000000;
    cursor: pointer;
}
.btn_con ul li:nth-child(1) {
    border: 0;
    border-bottom: 2px solid #000000;
    background-color: #F2F2F2;
}
.btn_con ul li:nth-child(1) .btn_link {
    color: #C0C0C0;
    border-right: 1px solid #FFFFFF;
}
.btn_con ul li:nth-child(2) {
    border: 0;
    border-bottom: 2px solid #000000;
    background-color: #F2F2F2;
}
.btn_con ul li:nth-child(2) .btn_link {
    color: #C0C0C0;
    border-right: 1px solid #FFFFFF;
}
.btn_con ul li:nth-child(3) {
    border: 0;
    border-bottom: 2px solid #000000;
    background-color: #F2F2F2;
}
.btn_con ul li:nth-child(3) .btn_link {
    color: #C0C0C0;
    border-right: 1px solid #FFFFFF;
}
.btn_con ul li:nth-child(4) {
    border-bottom: 0;
}
.btn_con ul li .btn_link {
    width: 100%; height: 100%;
    line-height: 75px;
    font-size: 24px; font-weight: 900;
    display: block;
}

.container {
    margin: auto; padding-top: 45px;
    width: 1184px;
}
.container img {
    width: 100%;
}
.container h3 {
    margin: 70px 0 20px;
    font-size: 40px;
}
.container h4 {
    margin: 25px 0 20px;
    font-size: 30px;
}
.container p {
    font-size: 18px; font-weight: 500;
    line-height: 28px;
}
.container .campaign {
    display: flex;
    justify-content: space-between;
}
.container .campaign > div {
    width: 557px;
}